@import 'reset';
$background-color: rgba(48, 62, 77, 1.0);
$light-tan: #F4EFE7;
$light-mustard: #DEAB2B;
$tan: #ECE5D6;
$mustard: #C2940C;
$james-peach: #D99441;
$light-blue: #326173;
$navy: #0B1C26;



#map {
  height: 780px;
  width: 85%;
}
/* Optional: Makes the sample page fill the window. */
html, body {
  height: 100%;
  margin: 0;
  padding: 0px;
  background-color: $navy;
}

#Title-beer {
  font-size: 24px;
  font-weight: bold;
  color: white;
  padding: 20px 30px 10px 30px;
  font-family: 'Nunito', sans-serif;
  line-height: 1em;
}

#control{
		float: left;
		color: black;
		width: 15%;
    height: 780px;
    background-color: $light-blue;
	}

  #control h2, p {
    padding: 20px 30px 10px 30px;
    color: white;
    font-family: 'Open Sans', sans-serif;
    line-height: 1.3em;
  }

	#control input[type=text]{
		width: 68%;
		font-size: 16px;
		background-color: rgba(240, 234, 235, 1.0);
		border: 1px solid black;
    margin-left: 30px;
    margin-top: 10px;
	}

  #control button {
    margin-left: 30px;
      margin-top: 10px;
  }

.runButton  {
  margin-left: 30px;
  margin-top: 10px;
}

  .example {
    width: 100%;
    height: 800px;
    background-image: url("images/beer-line.jpg");
    background-size: cover;
  }

  .example-2 {
    width: 100;
  }

  .example-2 p {
    height: 300px;
    background-color:  $light-blue;
    font-weight: bold;
    font-size: 28px;
    text-align: center;
    padding: 10%;
    line-height: 1.5em;
    color: white;
    font-family: 'Open Sans', sans-serif;
  }

  .example-2 p span {
    line-height: 1.5em;
    font-size: 16px;
    color: white;
    text-align: center;
    padding: 10px;
  }

.content h2 {
  font-weight: bold;
  font-size: 18px;
}

.theList {
  padding: 60px;
}

.theList li {
  color: white;
  background-color: white;
  padding: 5px;
  display: inline-block;
  border: 2px solid $light-blue;
  border-radius: 10px;
  margin: 10px;
  font-family: 'Open Sans', sans-serif;
  max-width: 110px;
}

.theList li img {
  max-width: 100%;
}

header {
  height: 60px;
  width: 100%;
  background-color: $navy;
  // z-index: 1;
}

.nav-tabs {
  padding: 5px;
  font-size: 16px;
  padding-top: 5px;
  // border-bottom: 1px solid white;
  // border-left: 1px solid white;
  // border-right: 1px solid white;
  border-radius: 0 0 5px 5px;
  display: inline-block;
  float: left;
  height: 25%;
  width: 60px;
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 5px;
  margin-left: 10px;
  color: black;
  background-color: $light-blue;
  -webkit-transition: width 2s; /* For Safari 3.1 to 6.0 */
  transition: width 2s;
}

.nav-tabs-on {
  height: 35%;
}

.nav-tabs:hover {
  height: 35%;
}

.nav-2,
.nav-2-on {
  cursor: pointer;
  border-bottom: 1px solid $light-blue;
  color: $navy;
  padding: 5px;
  font-size: 16px;
  padding-top: 5px;
  display: inline-block;
  font-family: 'Open Sans', sans-serif;
  float: left;
  height: 20%;
  width: 90px;
  text-align: center;
  margin-top: 20px;
  margin-left: 10px;
  -webkit-transition: width 2s; /* For Safari 3.1 to 6.0 */
  transition: width 2s;
}

.nav-2-on {
  background-color: $light-blue;
  color: white;
  border-radius: 5px 5px 0 0 ;
}

.nav-2:hover {
  background-color: $light-blue;
  color: white;
  border-radius: 5px 5px 0 0 ;
}

header a:link,
header a:visited  {
    text-decoration: none;
    color: white;
}

.learnButton a:link,
.learnButton a:visited {
  text-decoration: none;
  color: white;
}

.history-bar {
  float: left;
  color: black;
  width: 15%;
  height: 780px;
  background-color:  $light-blue;
}

.abv-img {
  background-image: url('images/dkurth_beerme_ABV.png');
  width: 343px;
  height: 120px;
  margin: 0 auto;
  background-size: cover;
}

.ibu-img {
  background-image: url('images/dkurth_beerme_IBU.png');
  width: 343px;
  height: 120px;
  background-size: cover;
  margin: 0 auto;
}

#home-picture {
  background-image: url('images/beerToasting.jpg');
  background-size: cover;
  height: 780px;
  width: 100%;

}

.home-section {
  height: 40%;
  text-align: center;
  margin: 0 auto;
  padding: 5%;
  color: $navy;
  background-color: white;
}

.home-section h1 {
  font-family: 'Nunito', sans-serif;
  font-size: 28px;
}

#divs2center {
  padding-top: 200px;
  display: block;
}


#home-2 {
  border-radius: 25px;
  width: 40%;
}

#home-2 p {
  margin-top: 30px;
  font-size: 16px;
  font-family: 'Open Sans', sans-serif;
  color: $navy;
}

.ibu-content {
  width: 80%;
  border-radius: 25px;
  min-height:300px;
  margin: 0 auto;
  background-color: white;
  margin-top: 25px;
}


.abvIbu-bkgd {
  background-color: $light-blue;
  padding-top: 20px;
}

.beerImgText {
  color: white;
  background-color: $light-blue;
  padding: 5px;
  width: 100px;

  &.ellipses {
    font-size: 11px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }
}

.learnButton {
  width: 70%;
  font-size: 14px;
  background-color: $light-mustard;
  color: white;
  border-radius: 5px;
  border: none;
  margin-left: 30px;
  margin-top: 10px;
  padding: 10px;
  font-family: 'Open Sans', sans-serif;
  font-weight: bold;
}

.learnButton:hover {
  background-color: $mustard;
}

footer {
  background-color: $light-blue;
  width: 100%;
  height: 200px;
}

.abvIbu-content {
  padding-top: 90px;
  padding-left: 50px;
  padding-right: 50px;
  // text-align: center;
}

.abvIbu-content h2 {
  font-weight: bold;
  padding-bottom: 10px;
  color: $navy;
  font-family: 'Nunito', sans-serif;
  font-size: 22px;
}

.abvIbu-content h3 {
  color: $navy;
  font-family: 'Open Sans', sans-serif;
  line-height: 1.4em;
}


@media only screen and (max-width: 1100px) {
  #control{
  		float: left;
  		color: black;
  		width: 20%;
      height: 781px;
      background-color: $light-blue;
  	}

    #control h2, p {
      padding: 10px 20px 5px 20px;
      color: white;
      font-family: 'Open Sans', sans-serif;
      line-height: 1.3em;
      font-size: 16px;
    }

  	#control input[type=text]{
  		width: 60%;
  		font-size: 16px;
  		background-color: rgba(240, 234, 235, 1.0);
  		border: 1px solid black;
      margin-left: 20px;
      margin-top: 10px;
  	}

    #control button {
      margin-left: 20px;
        margin-top: 10px;
    }

    #map {
      height: 781px;
      width: 80%;
    }

    #divs2center {
      padding-top: 180px;
      display: block;
    }
    .abvIbu-content {
      padding-top: 90px;
    }
    .ibu-content {
      min-height:350px;
    }
}

@media only screen and (max-width: 800px) {
  #control{
      float: left;
      color: black;
      width: 25%;
      height: 781px;
      background-color: $light-blue;
    }

    #control h2, p {
      padding: 10px 20px 5px 10px;
      color: white;
      font-family: 'Open Sans', sans-serif;
      line-height: 1.3em;
      font-size: 14px;
    }

    #control input[type=text]{
      width: 60%;
      font-size: 16px;
      background-color: rgba(240, 234, 235, 1.0);
      border: 1px solid black;
      margin-left: 10px;
      margin-top: 10px;
    }

    #control button {
      margin-left: 10px;
        margin-top: 10px;
    }

    #map {
      height: 781px;
      width: 75%;
    }
    .learnButton {
      width: 60%;
      font-size: 14px;
      margin: 0 auto;
      display: block;
      background-color: $light-mustard;
      color: white;
      border-radius: 5px;
      border: none;
      margin-top: 10px;
      padding: 10px;
      font-family: 'Open Sans', sans-serif;
      font-weight: bold;
    }
    .abvIbu-content {
      padding-top: 190px;
    }
    .ibu-content {
      min-height:400px;
    }
}

@media only screen and (max-width: 500px) {
  #control{
      // float: left;
      color: black;
      width: 100%;
      height: 350px;
      background-color: $light-blue;
    }

    #control h2, p {
      padding: 20px 30px 10px 20px;
      color: white;
      font-family: 'Open Sans', sans-serif;
      line-height: 1.3em;
      font-size: 16px;
    }

    #control input[type=text]{
      width: 40%;
      font-size: 16px;
      background-color: rgba(240, 234, 235, 1.0);
      border: 1px solid black;
      margin-top: 30px;
      margin-bottom: 20px;
      display: block;
    }

    #control button {
      margin-top: 10px;
      margin-bottom: 5px;
      width: 61%;
      display: block;
    }

    #map {
      height: 500px;
      width:100%;
    }
    #divs2center {
      padding-top: 100px;
      display: block;
    }
    .learnButton {
      width: 60%;
      font-size: 14px;
      display: block;
      background-color: $light-mustard;
      color: white;
      border-radius: 5px;
      border: none;
      margin-top: 10px;
      padding: 10px;
      font-family: 'Open Sans', sans-serif;
      font-weight: bold;
    }
    .abvIbu-content {
      padding-top: 200px;
      padding-bottom: 40px;
    }

    .ibu-content {
      min-height:500px;
    }

    #textZip {
      text-align: center;
    }
}

@media only screen and (min-width: 1500px) {
  #control{
  		float: left;
  		color: black;
  		width: 15%;
      height: 950px;
      background-color: $light-blue;
  	}

    #control h2, p {
      padding: 10px 20px 5px 20px;
      color: white;
      font-family: 'Open Sans', sans-serif;
      line-height: 1.3em;
      font-size: 16px;
    }

  	#control input[type=text]{
  		width: 60%;
  		font-size: 16px;
  		background-color: rgba(240, 234, 235, 1.0);
  		border: 1px solid black;
      margin-left: 20px;
      margin-top: 10px;
  	}

    #control button {
      margin-left: 20px;
        margin-top: 10px;
    }

    #map {
      height: 950px;
      width: 85%;
    }

    #divs2center {
      padding-top: 180px;
      display: block;
    }
    .abvIbu-content {
      padding-top: 90px;
    }
    .ibu-content {
      min-height:350px;
    }

    #home-picture {
      background-image: url('images/beerToasting.jpg');
      background-size: cover;
      height: 950px;
      width: 100%;
    }
    .abvIbu-bkgd {
      background-color: $light-blue;
      padding-top: 20px;
      min-height: 950px;
    }
}
